import * as yup from 'yup';

export const signUpValidation = yup.object({
  email: yup
    .string()
    .email('  .')
    .required(' .'),
  username: yup
    .string()
    .required(' .')
    .max(15, ' 15  .')
    .min(2, ' 2  .'),
  password: yup
    .string()
    .required(' .')
    .max(15, ' 15  .')
    .min(4, ' 4  .'),
  password2: yup
    .string()
    .oneOf([yup.ref('password'), null], '  .'),
  gender: yup.string().required("A radio option is required"),
  phone: yup
    .string()
    .required('  .'),
});

